@import url(https://fonts.googleapis.com/css?family=Bilbo&display=swap);
.card {
	width: 150px;
	height: 150px;
	box-shadow: 0 6px 6px rgba(0, 0, 0, 0.4);
	margin: 20px;
	border-radius: 10px;
	overflow: hidden;
	transition: transform 0.5s;
}

.card:hover {
	transform: scale(1.1);
}

img {
	width: 100%;
	height: 100%;
}

@media only screen and (max-width: 1024px) {
	.card {
		width: 120px;
		height: 120px;
		margin: 10px;
		margin-top: 50px;
	}
}

@media only screen and (max-width: 768px) {
	.card {
		width: 100px;
		height: 100px;
		margin: 20px;
		margin-top: 0;
	}
}

@media only screen and (max-width: 430px) {
	.card {
		width: 80px;
		height: 80px;
		margin: 6px 6px 15px;
		box-shadow: 0 3px 6px rgba(255, 255, 255, 0.384);
	}

	.card:hover {
    transform: scale(1);
	}
}

.cards-container {
	display: flex;
	flex-wrap: wrap;
	margin: 0 auto;
	width: 60%;
}

@media only screen and (max-width: 1024px) {
	.cards-container {
		width: 66%;
	}
}

@media only screen and (max-width: 768px) {
	.cards-container {
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 650px;
		flex: 1 0 100%;
	}
}

.guide-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-family: "Bilbo", cursive;
	color: #fff;
	width: 30%;
}

.instructions {
	font-size: 1.8rem;
	letter-spacing: 2px;
	text-align: center;
	padding: 0 40px;
}

header {
	font-size: 4rem;
	color: gold;
	font-weight: bold;
	letter-spacing: 2px;
	text-align: center;
	text-shadow: 2px 2px 10px rgba(149, 146, 115, 0.5);
}

h3 {
	font-size: 2.5rem;
	text-align: center;
	letter-spacing: 2px;
	font-weight: bold;
}

span {
	color: greenyellow;
	font-size: 3rem;
}

@media only screen and (max-width: 1024px) {
	header {
		font-size: 2.6rem;
	}

	.guide-container {
		margin-left: 20px;
		margin-top: 100px;
		justify-content: unset;
	}

	.instructions {
		font-size: 1.5rem;
		padding: 0;
	}
}

@media only screen and (max-width: 768px) {
	.guide-container {
		margin-left: 0;
		margin-top: 0;
		flex: 1 0 100%;
	}

	.instructions {
		margin: 0 90px;
	}

	.score h3 {
		margin: 0;
	}
}

@media only screen and (max-width: 430px) {
	header {
    font-size: 2rem;
    text-shadow: 1px 1px 3px #000;
	}

	.guide-container {
		margin-left: 0;
		margin-top: 10px;
		flex: 1 0 100%;
	}

	.instructions {
		margin: 0;
		font-size: 1.3rem;
    letter-spacing: 3px;
    text-shadow: 1px 1px 2px #000;
	}

	.score h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  span {
    font-size: 1.8rem;
  }
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
}
